import { Component, OnInit } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { SmartTableService } from '../../../@core/data/smart-table.service';
import { Column } from 'ng2-smart-table/lib/data-set/column';


import { DashboardService } from '../../../@core/data/services/dashboard.service';
import { Router } from '@angular/router';


@Component({
  selector: 'ngx-smart-table',
  templateUrl: './evidence-table.component.html',
  styles: [`
    nb-card {
      transform: translate3d(0, 0, 0);
    }
  `],
})
export class SmartTableComponent  implements OnInit{


  
  data : any = {};
  interval: any;

  ngOnInit()
  {
    this.refreshData();
    this.interval = setInterval(() => { 
        this.refreshData(); 
    }, 20000);
  }
  constructor(private eService :DashboardService, private router:Router)
   {
    
   }

   refreshData(){
    this.eService.fetchDashboardLiveData().subscribe(x=>{
      console.log(x);
      this.data=x;
    });
}

   public onRefresh() {
     
    this.router.routeReuseStrategy.shouldReuseRoute = function(){return false;};
  
    let currentUrl = this.router.url + '?';
  
    this.router.navigateByUrl(currentUrl)
      .then(() => {
        this.router.navigated = false;
        this.router.navigate([this.router.url]);
      });
    }
}



